/**  Store */
export const Loading = {
  namespaced: true,
  state: () => ({
    /**   */
    isLoading: false,
  }),
  mutations: {
    SET_LOADING(state, isLoading) {
      state.isLoading = isLoading;
    },
  },
  actions: {
    FETCH_LOADING({ commit }, values) {
      commit('SET_LOADING', values);
    },
  },
};
